const SignUpForm = () => {
    const { firstName, lastName, gender, email, password, toggleAgree, agree, onSubmit, message } = useSignUpContext();
    return (
        <Container>
            <TitleExtended 
                className={"signup-title"}
                title={"sign up"}
                error={message}
            />
            <InputTitleText 
                title={"first name"}
                { ...firstName }
            />
            <InputTitleText 
                title={"last name"}
                { ...lastName }
            />
            <InputCell
                title={"gender"}
                input={
                    <>
                        <InputRadio 
                            id={1}
                            label={"male"}
                            value={"male"}
                            name={"gender"}
                            onChange={gender.onChange}
                            defaultChecked={gender.value === "male"}
                        />
                        <InputRadio 
                            id={2}
                            value={"female"}
                            label={"female"}
                            onChange={gender.onChange}
                            name={"gender"}
                            defaultChecked={gender.value === "female"}
                        />
                    </>
                }
            />
            <Selector title={"Age"} />
            <Selector title={"Nationality"} />
            <Address title={"Address"}/>
            <InputTitleText 
                title={"Email"}
                { ...email }
            />
            <InputTitleText 
                title={"password"}
                type={"password"}
                { ...password }
            />
            <TextArea 
                title={"Agreement"}
                value={AgreementText}
            />
            <AgreeCheckbox 
                title={"I agree"}
                onChange={toggleAgree}
                isAgree={agree}
                name={"agree"}
            />
            <ButtonBox>
                <Link to={"/"}>
                    <CircleButtonLink
                        color={"gold"}
                        disabled={false}
                        value={"CANCEL"}
                        onClick={() => {}}
                    />
                </Link>
                {
                    agree && (
                        <CircleButton 
                            color={"green"}
                            disabled={false}
                            value={"SUBMIT"}
                            onClick={onSubmit}
                        />
                    )
                }
                
            </ButtonBox>
        </Container>
    );
}

export default SignUpForm;